.App {
  text-align: center;
}

.App-header {
  height: 50px; 
  width: DisplayWidth;
  background-color:black;
  margin-bottom:100px; 
}

.App-header h1 {
  font-size:20;
  color: black;
 
}
.App-header h2 {
  font-size:20;
  color: black;
 
}
.App-header a {
  list-style:none ;

}
.App-header a li {
color: #61dafb;
list-style-position: unset;
}

.App-link {
  color: #61dafb;
  
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
